html {
    font-family: 'Noto Sans TC', sans-serif;
    width: 100%;
    height: 100%;
    -webkit-font-smoothing: antialiased;
}

body {
    width: 100%;
    height: 100%;
    background-color: #fff;
}

@mixin rsp-img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
}

.container {
    @include wrap;
}

.wrap {
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    transition: all 300ms ease;
    height: auto;
    &.logo {
        height: calc(56.25vw - 30px);
        overflow: hidden;
    }
}